import { Component, ViewChild, AfterViewInit } from '@angular/core';
import { AceEditorComponent } from 'ng2-ace-editor';
import { MatInputModule } from '@angular/material/input';
import {MatSlideToggleModule} from '@angular/material/slide-toggle';
import {MatDialog, MAT_DIALOG_DATA} from '@angular/material/dialog';

var pluralize = require('pluralize');

class Field {
  uuid: string;
  type: string;
  name: string;
  originalName: string;
  root: Field = null;
  bArray: boolean = false;
  constructor(_type: string, _name: string, _struct: Field, _bArray?: boolean) {
    this.originalName = _name;
    this.name = _name;
    this.type = _type;
    this.root = _struct;
    this.bArray = _bArray;
    this.uuid = generateUUID();
  }
  getPrefixedName(name?:string, pluralized?:boolean):string{
    if (!this.root){
      return this.name + (name ? name : '');
    }else{
      return (this.root.getPrefixedName((name ? name : ''), this.root.bArray && pluralized )) + (this.name ?  ( pluralized ? pluralize.singular(capitalize(this.name)) : capitalize(this.name) ) : '');
    }
  }
  getName(pluralized?:boolean){
    return (pluralized ? pluralize.singular(this.name) : this.name);
  }
  setArray(_bArray:boolean){
    this.bArray = _bArray;
  }
}

class Parameter{
  _name: string;
  _type: string;
  _bArray: boolean;

  arg():string{
    return '_'+this._name;
  }
  name():string{
    return this._name;
  }
  type():string{
    return this._type;
  }
  constructor(name:string, type: string, bArray?:boolean){
    this._name = name;
    this._type = type;
    this._bArray = bArray;
  }
}


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements AfterViewInit {
  
  constructor(public dialog: MatDialog){

  }

  title = 'json2ue';
  generateType = 'Struct';

  optionsPrefixes = true;
  optionsSerializable = true;
  optionsBlueprintable = true;
  optionsPluralizable = true;
  optionsDefaultConstructor = true;
  optionsMakeCategories = true;

  jsonValue:string = '';

  fields: Array<Field> = [];
  className:string = '';

  @ViewChild('jsoneditor', {static: false }) jsonEditor : AceEditorComponent;
  
  @ViewChild('cppeditor', {static: false }) cppEditor : AceEditorComponent;
  
  howToUseIt(){
    window.open('https://youtu.be/riq_avp-YQw', "_blank");
  }

  openDialog() {
    this.dialog.open(DialogDataExampleDialog);
  }

  ngAfterViewInit() {

    let cmp = this;
    this.jsonEditor.getEditor().setOptions({
      showLineNumbers: false,
      tabSize: 2,
      maxLines: Infinity,
      autoUpdateContent: true
    });

    this.jsonEditor.mode = 'json';


    this.jsonEditor.registerOnChange(function(text){

      //this.jsonEditor.setValue(JSON.stringify(text, null, '\t'));

      cmp.handleData(text);
    })

    this.jsonEditor.value =
`
{
  "users": [
    {
      "name": "John",
      "car": {
        "name": "Skoda",
        "model": "Octavia",
        "cost": 12000
      },
      "friends": [
        "Michael",
        "Rose",
        "Nikita"
      ],
      "points": 35.42,
      "deleted": false
    }
  ]
}
`

    this.cppEditor.getEditor().setOptions({
      showLineNumbers: true,
      tabSize: 2,
      readOnly: true,
      maxLines: Infinity
 
    });
    
    require('../assets/ue4mode.js');

    this.cppEditor.mode = 'c_ue4';

    this.cppEditor.registerOnChange(function(text){
      
    })
    
    this.handleData(this.jsonEditor.value);
  }
  onClassNameChanged(val){
    
    this.className = val;

    this.handleData(this.jsonEditor.value);
  }

  onPrefixesToggle(val){
    this.optionsPrefixes = val;

    this.handleData(this.jsonEditor.value);
  }

  onSerializableToggle(val){
    this.optionsSerializable = val;

    this.handleData(this.jsonEditor.value);
  }

  onDefaultConstructorToggle(val){
    this.optionsDefaultConstructor = val;

    this.handleData(this.jsonEditor.value);
  }

  onMakeCategoriesToggle(val){
    this.optionsMakeCategories = val;

    this.handleData(this.jsonEditor.value);
  }

  onBlueprintableToggle(val){
    this.optionsBlueprintable = val;
    this.handleData(this.jsonEditor.value);
  }

  onPluralizableToggle(val){
    this.optionsPluralizable = val;
    this.handleData(this.jsonEditor.value);
  }
  lastData = null;

  copyToClipboard = str => {
    const el = document.createElement('textarea');
    el.value = str;
    el.setAttribute('readonly', '');
    el.style.position = 'absolute';
    el.style.left = '-9999px';
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
  };

  async handleData(data) {
    // do nothing when data equal with last data
    if (this.lastData == data){
      return;
    }

    if (data.length > 10000000){
      console.error('too long input');
      this.cppEditor.value = 'Too long input, try to past smaller part of response';
      return false;
    }
    if(!IsJsonString(data)){
      console.error('not valid JSON');
      this.cppEditor.value = 'NOT VALID JSON';
      return false;
    }

    var obj = JSON.parse(data);

    if(Math.abs(data.length - this.jsonValue.length) > 4){
      this.jsonEditor.value = JSON.stringify(obj, null, 2);
    }

    this.jsonValue = data;
    this.fields = [];
    this.parse(obj);
    var cppValue = this.generateCPPValue();
    if (data.length > 10000){
      this.copyToClipboard(cppValue);
      this.cppEditor.value = cppValue.substring(0, 10000);
    }else{
      this.cppEditor.value = cppValue;
    }
  }

  generateCPPValue():string{

    var text = '';
    var that = this;

    this.fields.forEach(function(field:Field){
      text = that.generateTextForField(field, text);
    });

    return text;
  }

  generateTextForField(field:Field, text: string):string{
    if (field.type == 'struct'){
      text = this.generateWrapperForStruct(field, text); 
    }
    return text;
  }

  getNameOfStruct(struct:Field):string{
    if (this.optionsPrefixes){
      return struct.getPrefixedName('', this.optionsPluralizable);
    }else{
      return struct.getName(this.optionsPluralizable);
    }
  }

  generateDefaultConstructor(struct:Field): string{

    var text = `
  F${capitalize(this.getNameOfStruct(struct))}() {};`

    if (!this.optionsDefaultConstructor){
      return text + `
      `;
    }

    var args:string = '';
    var assignment:string = ``;

    this.getParameters(struct).forEach((param: Parameter)=>{
      if (args.length > 0){
        args += `, `
      }
      if (assignment.length > 0){
        assignment += `
    `;
      }
      var type:string = param.type();

      if (param._bArray){
        type = `TArray<${param.type()}>`;
      }

      args += `${type} ${param.arg()}`;
      assignment += `${param.name()} = ${param.arg()};`;
    });

    

    text += `

  F${capitalize(this.getNameOfStruct(struct))}( ${args} ){

    ${assignment}
  
  }
  `;

    return text;
  }

  generateSerialization(struct:Field): string{
    if (!this.optionsSerializable){
      return '';
    }

    var assignment = '';
    this.getParameters(struct).forEach((param: Parameter)=>{

      if (assignment.length > 0){
        assignment += `
    `;
      }

      assignment += `${param.name()} = _tmp${this.getNameOfStruct(struct)}.${param.name()};`;
    });

    var text = 
  `
  /* Don't Forget to setup your project
  Add #include "Runtime/JsonUtilities/Public/JsonObjectConverter.h" in 
  file with this structs.
  Also you need add "Json", "JsonUtilities" in Build.cs */

  F${capitalize(this.getNameOfStruct(struct))}(FString _json_){
    F${capitalize(this.getNameOfStruct(struct))} _tmp${this.getNameOfStruct(struct)};
    
		FJsonObjectConverter::JsonObjectStringToUStruct<F${capitalize(this.getNameOfStruct(struct))}>(
		_json_,
		&_tmp${this.getNameOfStruct(struct)},
    0, 0);
    
    ${assignment}
  }
  `;

    return text;
  }

  generateWrapperForStruct(struct:Field, text: string):string{

    var fields = this.getFieldsWithStruct(struct);
    var fieldsContent = ``;
    var that = this;

    fields.forEach((field:Field)=>{
      fieldsContent += that.generateWrapperForField(field);
    });

    var wrapper:string = 
`
USTRUCT(${this.optionsBlueprintable ? 'BlueprintType' : ''})
struct F${ this.optionsPrefixes ? struct.getPrefixedName('', that.optionsPluralizable) : capitalize(struct.getName(that.optionsPluralizable)) }
{

  GENERATED_BODY()
${fieldsContent}${this.generateDefaultConstructor(struct)}${!struct.root ? this.generateSerialization(struct) : ''}
};
`;

    return wrapper + text;
  }

  generateWrapperForField(field:Field):string{

    var categoryPropertyString = this.optionsMakeCategories ? `Category="JSON|${this.getRootClassName()}${field.root.getName(true) != this.getRootClassName() ? '|' + field.root.getName(true) : ''}", ` : '';
    if (field.type == 'struct'){
      if (field.bArray){
return `
  UPROPERTY(${categoryPropertyString}${this.optionsBlueprintable ? 'EditAnywhere, BlueprintReadWrite' : ''})
  TArray<F${this.optionsPrefixes ? field.getPrefixedName('', this.optionsPluralizable) : capitalize(field.getName(this.optionsPluralizable))}> ${field.getName()};
`;
      }else{
return `
  UPROPERTY(${categoryPropertyString}${this.optionsBlueprintable ? 'EditAnywhere, BlueprintReadWrite' : ''})
  F${this.optionsPrefixes ? field.getPrefixedName('', this.optionsPluralizable) : capitalize(field.getName(this.optionsPluralizable))} ${field.getName()};
`;
      }
    }
    if(field.bArray){
return `
  UPROPERTY(${categoryPropertyString}${this.optionsBlueprintable ? 'EditAnywhere, BlueprintReadWrite' : ''})
  TArray<F${this.optionsPrefixes ? field.getPrefixedName('', this.optionsPluralizable) : capitalize(field.getName(this.optionsPluralizable))}> ${field.getName()};
`;
    }else{
return `
  UPROPERTY(${categoryPropertyString}${this.optionsBlueprintable ? 'EditAnywhere, BlueprintReadWrite' : ''})
  ${field.type} ${field.getName()};
`;
    }

  }

  getParameters(struct:Field):Array<Parameter>{
    var params:Array<Parameter> = [];

    var fields:Array<Field> = this.getFieldsWithStruct(struct);

    fields.forEach((field:Field)=>{
      var type = field.type;
      if (type == 'struct'){
        type = `F${this.getNameOfStruct(field)}`;
      }
      params.push(new Parameter(field.getName(), type, field.bArray));
    });

    return params;
  }

  getFieldsWithStruct(struct:Field):Array<Field>{
    var fields:Array<Field> = [];

    this.fields.forEach((field)=>{
      if (!field.root)
        return
      if (field.root.uuid == struct.uuid){
        fields.push(field);
      }
    })

    return fields;
  }

  pushUnique(field: Field){
    if (field.root){
      var bDuplicated = false;
      this.fields.forEach((item)=>{
        if ((item.root == field.root) && (item.originalName == field.originalName)){
          bDuplicated = true;
        }
      });

      if (bDuplicated){
        return;
      }
    }
    this.fields.push(field);
  }

  parse(val: any, name?: string, struct?: Field, bArray?:boolean) {

    if (val instanceof Object) {
      var newField : Field;

      if (!name && !struct){
        newField = new Field('struct', `${this.className ? capitalize(this.className) : `My${this.generateType}`}`, null);
      }else{
        newField = new Field('struct', ''+name, struct, bArray);
      }
      
      this.pushUnique(newField);

      for (let key in val) {
        let value = val[key];
        if (Array.isArray(value)) {
          // var arrayField = new Field('struct', capitalize(key), newField, true);
          // this.fields.push(arrayField);

          var cmp = this;

          cmp.parse(value[0], key, newField, true);

        } else {
          this.parse(value, key, newField);
        }
      }

      //text = this.generateStructWrapper(text, name);
    } else {
      this.parseOneField(val, name, struct, bArray);
    }

  }

  parseOneField(field: any, name?: string, struct?: Field, bArray?:boolean){
    var text: string = "";
    var type: string = "";
    if (typeof field === 'string') {
      type = "FString";
    } else
      if (typeof field === 'number') {
        if (field % 1 === 0) {
          if (Math.abs(field) >= 2147483648){
            type = "int64";
          }else{
            type = "int32";
          }
          
        } else {
          type = "float";
        }
      } else
      if (typeof field === 'boolean') {
        type = "bool"
      } else {
        console.error('NOT UNDESTANDABLE TYPE!');
        return "";
      }
    
    var newField = new Field(type, name, struct, bArray);
    this.fields.push(newField);

    //return text;
  }

  getRootClassName():string{
    return `${this.className ? capitalize(this.className) : `My${this.generateType}`}`;
  }

  generateStructWrapper(body: string, fieldname?: string): string {

    var text: string = ``;
    var className: string = ``;

    if (!fieldname){
      className = `F${this.className ? capitalize(this.className) : `My${this.generateType}Name`}`;
    }else{
      className = `F${capitalize(fieldname)}`;
    }
    

    text += `
// AUTOGENERATED
USTRUCT(${this.optionsBlueprintable ? 'BlueprintType' : ''})
struct ${className}
{

  GENERATED_BODY()

  ${body}

  ${className}(FString JsonString){

    TSharedRef<TJsonReader<>> Reader = TJsonReaderFactory<>::Create(Response->GetContentAsString());
    
    if (FJsonSerializer::Deserialize(Reader, JsonObject))
    {
      ${this.getBodyParser()}
      int32 recievedInt = JsonObject->GetIntegerField("customInt");

      GEngine->AddOnScreenDebugMessage(1, 2.0f, FColor::Green, FString::FromInt(recievedInt));

    }

  }

}


`;

    return text;

  }

  getBodyParser():string{
    return 'BODY';
  }
}

@Component({
  selector: 'dialog-data-example-dialog',
  template: `<h1 mat-dialog-title>Contact</h1>
  <div mat-dialog-content>
    <p>Contact email: pogorelowmax@gmail.com</p>
  </div>`,
})
export class DialogDataExampleDialog {
  constructor() {}
}

const capitalize = (s) => {
  if (typeof s !== 'string') return ''
  return s.charAt(0).toUpperCase() + s.slice(1)
}

function IsJsonString(str) {
  try {
      JSON.parse(str);
  } catch (e) {
      return false;
  }
  return true;
}

function generateUUID() { // Public Domain/MIT
  var d = new Date().getTime();//Timestamp
  var d2 = (performance && performance.now && (performance.now()*1000)) || 0;//Time in microseconds since page-load or 0 if unsupported
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16;//random number between 0 and 16
      if(d > 0){//Use timestamp until depleted
          r = (d + r)%16 | 0;
          d = Math.floor(d/16);
      } else {//Use microseconds since page-load if supported
          r = (d2 + r)%16 | 0;
          d2 = Math.floor(d2/16);
      }
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
  });
}
